.login {
    height: 100vh;
    background-color: #de9494;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.login .wrapper {
    background-color: #fff;
    padding: 24px;
    border-radius: 4px;
}
.login h3 {
    text-align: center;
}
.login label {
    display: block;
    margin: 12px;
}
.login input {
    width:  210px;
    height: 30px;
}
.login .oper {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login .oper button {
    outline: none;
    padding: 6px 24px;
    margin: 6px 0;
    background-color: burlywood;
    border: 0;
    cursor: pointer;
}

