.oper {
    margin: 20px;
    display: flex;
    justify-content: center;
}
.oper label {
    margin-left: 20px;
}
.oper button {
    background-color: #6ac1b2;
    color: #fff;
    padding: 6px 12px;
    outline: none;
    border: 0;
}
.wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.wrap li {
    width: 20vw;
    margin: 12px;
}

.wrap li img {
    width: 100%;
    height: 200px;
}
.wrap li  a {
    text-decoration: none;
}
.h-title {
    font-size:16px;
    height: 36px;
    margin: 5px 0;
    width: 100%;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    /* white-space: nowrap; */
    color: #333;
}
.roomNum {
    font-size: 13px;
    color: #6ac1b2;
}
.h-info {
    display: flex;
    line-height: 30px;
    justify-content: space-between;
    font-size:13px;
}
.h-price {
    color: orange;
}
.h-date{
    color: #666;
}
button.load {
    display: block;
    cursor: pointer;
    margin: 0 auto;
    background-color: #6ac1b2;
    color: #fff;
    padding: 6px 12px;
    outline: none;
    border: 0;
}