.swiper-container {
    height: 270px;
    background-color: #f0f0f0;
}
.swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
}
.hourse-info {
    padding: 20px;
}
.hourse-info .price {
    color: orange;
}
.hourse-info .content {
    background-color: #dbc5c5;
    line-height: 30px;
    height: 200px;
    overflow: hidden;
    position: relative;
    padding: 12px;
}
.hourse-info>p {
    margin: 12px 0;
}
.hourse-info .content.fold {
    height: unset;
}
.hourse-info .content span {
    position: absolute;
    width: 100%;
    text-align: center;
    bottom: 0;
    left: 0;
    background-color: aliceblue;
    cursor: pointer;
}
.publish form{
    display: flex;
    align-items: center;
    justify-content: center;
}
.publish form button {
    padding: 6px 12px;
    margin-left: 12px;
}
.publish textarea {
    width: 200px;
}
.username {
    font-size: 13px;
    color: #666;
    float: left;
    margin: 0 12px;
}
.comm-item{
    display: flex;
    justify-content: space-between;
    line-height: 30px;
    background-color: #dbc5c5;
    margin: 20px;
}
.comm-item textarea {
    width: 420px;
    height: 60px;
}
.comm-item button {
    cursor: pointer;
}
.create-time {
    font-size: 13px;
}
.btns {
    display: flex;
    align-items: center;
}
.btns button {
    padding: 4px 8px;
    margin: 0 12px;
}